<template>
  <div>
    <!-- 话术详情 -->
    <div style="width:1244px;height:660px;position:relative">
      <div style="height:650px;width:1170px;overflow-x:scroll">
        <div style="width:2000px">
          <div
            style="display:inline-block;width:281px;height:641px;background:rgba(225, 230, 241, 1);position:relative;"
            v-for="(item1,index1) in nodeList"
            :key="index1"
          >
            <div style="overflow-y:scroll;width:281px;height:641px;padding-bottom:80px;">
              <div
                :class="choiceId==item.id?'nodeCss':'nodeCss1'"
                v-for="(item,index) in item1"
                :key="index"
                @click="choice(index1,item)"
              >
                <div
                  style="overflow: hidden;
                  text-overflow: ellipsis;
                  display: -webkit-box;
                  -webkit-line-clamp: 2;
                  line-clamp: 2;
                  -webkit-box-orient: vertical;"
                >
                  <span
                    style="font-size:13px;color: #333333;padding-left:10px;"
                    v-for="(item1,index1) in item.nodeTextList"
                    :key="index1"
                  >{{item1.content}}</span>
                </div>

                <div
                  style="position:absolute;bottom:6px;width:100%;display:flex;justify-content: space-between"
                >
                  <span
                    style="color: rgba(89, 101, 151, 0.8);font-size:10px;padding-left:10px;"
                  >触发条件: {{item.term==1?'用户选择':item.term==2?'用户输入':'无'}}</span>
                  <a
                    style="color: #2A60E4;font-size:10px;font-weight:800;padding-right:10px;"
                    @click="gotoDetail(item1,item.id)"
                  >查看详情</a>
                </div>
              </div>
            </div>

            <div
              style="position:absolute;bottom:20px;left:0;background-color: #fff;width:262px;height:42px;margin:0 auto;margin-top:10px;text-align:center;border:1px solid black;border-style:dashed;"
            >
              <div style="margin-top:10px" @click="addContent(item1)">
                <a>+新增</a>
              </div>
            </div>

            <div
              style="position:absolute;bottom:0;left:0;width:281px;height:20px;background:#eee;color:#07175D"
              v-if="item1.creatTime"
            >创建于 {{item1.creatTime}}</div>
          </div>
          <div
            style="display:inline-block;position:relative;width:281px;height:641px;border:1px solid black;border-style:dashed;right:20px;background:#fff;margin-left:20px"
            v-if="isShowBox"
          >
            <Button
              @click="addNodes"
              type="primary"
              icon="md-add"
              style="position:absolute;left:50%;top:50%;margin-left:-47px;margin-top:-10px"
            >新增节点</Button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "nodelist",
  data() {
    return {
      level: "",
      choiceId: "",
      modelEdit: false,
      isShowBox: false,
      editNode1: {
        editNode1: []
      }
    };
  },
  watch: {
    type(val) {
      if (val) {
        this.isShowBox = false;
      }
    }
  },
  props: ["nodeList", "verbalTrickId", "momentChild", "type"],
  methods: {
    choice(index1, item) {
      this.level = index1;
      console.log("nnn", index1);
      this.$axios
        .post("api/verbalTrickNode/getVerbalTrickNodeList", {
          verbalTrickId: this.verbalTrickId,
          stageId: this.momentChild,
          upId: item.id
        })
        .then(res => {
          console.log(res);
          if (res.data.data.length > 0) {
            this.isShowBox = false;
            res.data.data.upId = item.id;
            if (this.choiceId != item.id) {
              this.nodeList[index1 + 1] = res.data.data;
            }
            this.nodeList.splice(index1 + 2);
            this.choiceId = item.id;
          } else {
            this.choiceId = item.id;
            this.nodeList.splice(index1 + 1);
            this.isShowBox = true;
          }
          this.$emit("changeNodeList", {
            level: this.level,
            nodeList: this.nodeList
          });
        });
    },
    addContent(item1) {
      let obj = {
        verbalTrickId: this.verbalTrickId,
        momentChild: this.momentChild
      };
      if (item1.upId) {
        obj.upId = item1.upId;
      }
      this.$router.push({
        path: "/addnode",
        query: obj
      });
    },
    addNodes() {
      this.$router.push({
        path: "/addnode",
        query: {
          verbalTrickId: this.verbalTrickId,
          momentChild: this.momentChild,
          upId: this.choiceId,
          index1: this.index1
        }
      });
      this.isShowBox = false;
    },
    gotoDetail(item1, id) {
      let obj = {
        id:id
      }
      if (item1.upId) {
        obj.upId = item1.upId;
      }
      this.$router.push({
        path: "/editnode",
        query: obj
      });
    }
  }
};
</script>
<style scoped>
.nodeCss {
  box-sizing: border-box;
  border-radius: 8px;
  background-color: rgba(161, 181, 255, 0.698039215686274);
  width: 255px;
  height: 76px;
  margin-left: 6px;
  margin-top: 10px;
  position: relative;
}
.nodeCss1 {
  border-radius: 8px;
  background-color: white;
  width: 255px;
  height: 76px;
  margin-left: 6px;
  margin-top: 10px;
  position: relative;
  box-sizing: border-box;
}
</style>


